<template>
  <div>
    <table class="table table-bordered w-50">
      <tr>
        <th>번호</th>
        <td>{{ item.qnaNo }}</td>
      </tr>
      <tr>
        <th>아이디</th>
        <td>{{ item.qnaUserid }}</td>
      </tr>
      <tr>
        <th>제목</th>
        <td>{{ item.qnaTitle }}</td>
      </tr>
      <tr>
        <th>날짜</th>
        <td>{{ item.qnaDatetime }}</td>
      </tr>
      <tr>
        <td colspan="2">
          {{ item.qnaContent }}
        </td>
      </tr>
    </table>

    <div v-if="item.replyUserid!=null">
      <table class="table table-bordered w-50">
      <tr>
        <th>답변ID</th>
        <td>{{ item.replyUserid }}</td>
      </tr>
      <tr>
        <th>답변내용</th>
        <td colspan="2">{{ item.replyContent }}</td>
      </tr>
      </table>
    </div>

    <br />
    <div class="text-center">
      <router-link to="/list"
        ><button class="btn btn-primary">목록</button></router-link
      >
      <router-link :to="'/update?no=' + item.qnaNo"
        ><button class="btn btn-primary">수정</button></router-link
      >
      <router-link :to="'/delete?no=' + item.qnaNo"
        ><button class="btn btn-primary">삭제</button></router-link
      >
    </div>
  </div>
</template>

<script>
import moment from 'moment';
import { mapGetters } from 'vuex';
export default {
  name: 'detail',
  computed: {
    ...mapGetters(['item'])
  },
  methods: {
    getFormatDate(regtime) {
      return moment(new Date(regtime)).format('YYYY.MM.DD HH:mm:ss');
    },
  },
};
</script>
