<template>
  <tr>
    <td>{{ qnaNo }}</td>
    <td>
      <router-link :to="`/read?qnaNo=${qnaNo}`">{{ qnaTitle }}</router-link>
    </td>
    <td>{{ qnaUserid }}</td>
    <td>{{ qnaContent }}</td>
    <td>{{ qnaDatetime }}</td>
  </tr>
</template>

<script>
import moment from 'moment';
export default {
  name: 'row',
  props: {
    qnaNo: Number,
    qnaTitle: String,
    qnaContent: String,
    qnaUserid: String,
    qnaDatetime: String,
    replyContent: String,
    replyDatetime: String,
    replyUserid: String    
  },
  methods: {
    getFormatDate(regtime) {
      return moment(new Date(regtime)).format('YYYY.MM.DD');
    },
  }
};
</script>
